// =============== –§–û–†–ú–ê –í –§–£–¢–ï–†–ï (–∑–∞—è–≤–∫–∞ –Ω–∞ –∑–≤–æ–Ω–æ–∫) ===============
const footerPhoneInput = document.getElementById('footer-phone');
const footerSubmitBtn = document.getElementById('footer-submit-btn');

if (footerSubmitBtn && footerPhoneInput) {
  footerSubmitBtn.addEventListener('click', () => {
    const phone = footerPhoneInput.value.trim();

    // –ü—Ä–æ—Å—Ç–∞—è –≤–∞–ª–∏–¥–∞—Ü–∏—è: –Ω–µ –ø—É—Å—Ç–æ –∏ —Ö–æ—Ç—è –±—ã 9 —Å–∏–º–≤–æ–ª–æ–≤ (–º–æ–∂–Ω–æ —É–ª—É—á—à–∏—Ç—å regex)
    if (!phone) {
      alert('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞');
      return;
    }

    if (phone.replace(/\D/g, '').length < 9) {
      alert('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞');
      return;
    }

    // –ò–º–∏—Ç–∞—Ü–∏—è —É—Å–ø–µ—à–Ω–æ–π –æ—Ç–ø—Ä–∞–≤–∫–∏
    alert(`–°–ø–∞—Å–∏–±–æ! –ú—ã —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –ø–æ –Ω–æ–º–µ—Ä—É ${phone} –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.`);

    // –û—á–∏—â–∞–µ–º –ø–æ–ª–µ
    footerPhoneInput.value = '';

    // –ó–¥–µ—Å—å –≤ –±—É–¥—É—â–µ–º –±—É–¥–µ—Ç —Ä–µ–∞–ª—å–Ω–∞—è –æ—Ç–ø—Ä–∞–≤–∫–∞:
    // fetch('/api/request-call', { method: 'POST', body: JSON.stringify({ phone }) })
  });
}
