(() => {
  const openBtns = document.querySelectorAll('[data-modal-open]');
  const closeBtns = document.querySelectorAll('.modal-close');
  const backdrops = document.querySelectorAll('.backdrop');

  console.log('–ú–æ–¥–∞–ª–∫–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω—ã');
  console.log('–ö–Ω–æ–ø–æ–∫ –æ—Ç–∫—Ä—ã—Ç–∏—è:', openBtns.length);
  console.log('–ë—ç–∫–¥—Ä–æ–ø–æ–≤:', backdrops.length);

  if (openBtns.length === 0) return;

  function openModal(modalId) {
    const backdrop = document.querySelector(`[data-modal="${modalId}"]`);
    if (!backdrop) {
      console.warn('–ë—ç–∫–¥—Ä–æ–ø –Ω–µ –Ω–∞–π–¥–µ–Ω:', modalId);
      return;
    }
    console.log('–û—Ç–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª–∫—É:', modalId);
    backdrop.classList.add('is-modal-open'); // ‚Üê –∏–∑–º–µ–Ω–∏–ª–∏
    document.body.style.overflow = 'hidden';
  }

  function closeModal(backdrop) {
    backdrop.classList.remove('is-modal-open'); // ‚Üê –∏–∑–º–µ–Ω–∏–ª–∏
    document.body.style.overflow = '';
  }

  openBtns.forEach(btn => {
    btn.addEventListener('click', e => {
      e.preventDefault();
      const modalId = btn.getAttribute('data-modal-open');
      console.log('–ö–ª–∏–∫ –ø–æ –∫–Ω–æ–ø–∫–µ:', btn.textContent.trim(), '‚Üí', modalId);
      openModal(modalId);
    });
  });

  closeBtns.forEach(btn => {
    btn.addEventListener('click', () => {
      const backdrop = btn.closest('.backdrop');
      if (backdrop) closeModal(backdrop);
    });
  });

  backdrops.forEach(backdrop => {
    backdrop.addEventListener('click', e => {
      if (e.target === backdrop) {
        closeModal(backdrop);
      }
    });
  });

  document.addEventListener('keydown', e => {
    if (e.key === 'Escape') {
      backdrops.forEach(backdrop => {
        if (backdrop.classList.contains('is-modal-open')) {
          // ‚Üê –∏–∑–º–µ–Ω–∏–ª–∏
          closeModal(backdrop);
        }
      });
    }
  });
})();
